<template>
	<div>
		<b-card>
			<div class="my-auto">
				<b-card-title class="mb-25">
					工单系统
				</b-card-title>
				<b-card-text class="mb-0">
					在正常工作时间内，我们的工单平均回复时间低于30分钟。
				</b-card-text>
			</div>
			<div class="TicketFrom mt-4" align="right">
				<b-form-group label-for="input-default">
					<b-form-input v-model="subject_txt" id="input-default" placeholder="请输入工单标题" />
				</b-form-group>
				<b-form-textarea v-model="content_txt" id="textarea-default" placeholder="请输入工单内容" />
				<b-button variant="flat-success" class="mt-2" @click="submitTicket" :disabled="btnstatus">
					<b-spinner v-show="btnstatus" small type="grow" />提交工单
				</b-button>
			</div>
		</b-card>
		<b-card>
			<b-table responsive :items="items" :fields="fields" class="mb-0">
				<template #cell(status)="data">
					<b-badge pill :variant="data.value == 0 ? 'danger' : data.value == 1 ? 'secondary' : 'success'">
						{{ data.value == 0 ? '待回复' : data.value == 1 ? '已关闭' : '已回复' }}
					</b-badge>
				</template>
				<template #cell(caozuo)="data">
					<b-button @click="TicketDetails(data.item.id)" variant="flat-primary"> 查看 </b-button>
					<b-button v-show="data.item.status != 1" @click="closeTicket(data.item.id)" variant="flat-primary"> 关闭 </b-button>
				</template>
				<template #cell(created_at)="data">
					{{ data.value | formatDate }}
				</template>
				<template #cell(updated_at)="data">
					{{ data.value | formatDate }}
				</template>
				<!-- Optional default data cell scoped slot -->
				<template #cell()="data">
					{{ data.value }}
				</template>
			</b-table>
		</b-card>
	</div>
</template>

<script>
import { BFormGroup, BFormInput, BCard, BCardTitle, BSpinner, BFormTextarea, BCardText, BButton, BTable, BBadge } from 'bootstrap-vue'
import { formatDate } from '@/libs/date'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
	data() {
		return {
			fields: [
				{ key: 'id', label: 'id' },
				{ key: 'subject', label: '标题' },
				{ key: 'status', label: '状态' },
				{ key: 'created_at', label: '创建时间' },
				{ key: 'updated_at', label: '最后回复' },
				{ key: 'caozuo', label: '操作' }
			],
			items: [],
			isPosting: true,
			subject_txt: '',
			content_txt: '',
			btnstatus: false
		}
	},
	filters: {
		formatDate(time) {
			time = time * 1000
			var date = new Date(time)
			return formatDate(date, 'yyyy-MM-dd hh:mm')
		}
	},
	components: {
		BFormGroup,
		BFormInput,
		BCard,
		BCardTitle,
		BFormTextarea,
		BCardText,
		BButton,
		BTable,
		BBadge,
		BSpinner
	},
	methods: {
		getTicketsRecords() {
			this.isPosting = true
			this.$http({
				url: '/api/v1/user/ticket/fetch',
				method: 'GET'
			})
				.then(res => {
					res = res.data

					if (res) {
						//   this.$store.commit("setUserSub", res);
						this.items = res.data
					}
					this.isPosting = false
				})
				.catch(err => {
					if (err.response.data.message) {
						this.$cookies.set('isLogin', 0)
						this.$router.push({
							name: 'Login'
						})
					}
					this.isPosting = false
				})
		},
		TicketDetails(id) {
			this.$router.push({ name: 'ticketdetails', params: { id: id } })
		},
		closeTicket(id) {
			console.log(id)
			this.$http({
				url: '/api/v1/user/ticket/close',
				method: 'POST',
				data: { id: id }
			})
				.then(res => {
					res = res.data.data

					if (res == true) {
						this.showToast('success', 'top-center', '关闭成功', '关闭工单成功')
						this.getTicketsRecords()
					}
					this.isPosting = false
				})
				.catch(err => {
					//msg(err.response.data.message)
					this.isPosting = false
				})
		},
		submitTicket() {
			let data = {
				level: 1,
				subject: this.subject_txt,
				message: this.content_txt
			}
			this.btnstatus = true
			this.$http({
				url: '/api/v1/user/ticket/save',
				method: 'POST',
				data: data
			})
				.then(res => {
					res = res.data.data

					if (res == true) {
						this.showToast('success', 'top-center', '提交成功', '发送工单成功')
						this.getTicketsRecords()
					}
					this.isPosting = false
					this.btnstatus = false
				})
				.catch(err => {
					this.showToast('danger', 'top-center', '提交失败', err.response.data.message)
					this.isPosting = false
					this.subject_txt = ''
					this.content_txt = ''
					this.btnstatus = false
				})
		},
		showToast(variant, position, title, text) {
			this.$toast(
				{
					component: ToastificationContent,
					props: {
						title: title,
						icon: 'InfoIcon',
						text: text,
						variant
					}
				},
				{
					position
				}
			)
		}
	},
	mounted() {
		this.getTicketsRecords()
	}
}
</script>
<style lang="scss">
.TicketFrom {
	textarea,
	input {
		border-radius: 0;
		border: 0 !important;
		border-bottom: 1px solid #d8d6de !important;
	}
}
</style>
